import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './account/login/login.component';
import { DashboardComponent } from './main/dashboard/dashboard.component';
import { SignupComponent } from './account/signup/signup.component';
import { NewsourceComponent } from './sources/newsource/newsource.component';
import { ViewsourcesComponent } from './sources/viewsources/viewsources.component';
import { ViewarticlesComponent } from './articles/viewarticles/viewarticles.component';
import { SinglearticleComponent } from './articles/singlearticle/singlearticle.component';
import { NewarticleComponent } from './articles/newarticle/newarticle.component';
import { AuthGuard } from './guards/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: LoginComponent,
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'signup',
    component: SignupComponent,
    canActivate: [AuthGuard]

  },
  {
    path: 'sources',
    component: ViewsourcesComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'newsource',
    component: NewsourceComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'viewarticles',
    component: ViewarticlesComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'article/:aid',
    component: SinglearticleComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'newarticle',
    component: NewarticleComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
