import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';




@Component({
  selector: 'app-viewsources',
  templateUrl: './viewsources.component.html',
  styleUrls: ['./viewsources.component.scss']
})
export class ViewsourcesComponent implements OnInit {

  term = '';
  public _url = 'http://localhost:8000';

  $sources: Object;

  constructor(private data: DataService, private modalService: NgbModal) { 
    this.data.getSources()
      .subscribe(data => {
        this.$sources = data;
      });

  }

  ngOnInit(): void {
  }

}
